.wrapper {
  width: 100%;
  height: 100%;
  max-width: 1160px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 0px;
  padding-right: 0px;
}

.container {
  width: 1440px;
  max-width: 100%;
}

@media only screen and (min-width: 768px) and (max-width: 1080px) {
  .wrapper {
    max-width: 100%;
    max-height: 100%;
    padding-left: 0px;
    padding-right: 0px;
  }
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .wrapper {
    max-width: 100%;
    max-height: 100%;
    padding-left: 0px;
    padding-right: 0px;
  }
}