.header {
  background-color: #FFFFFF;
  max-width: 100%;
  height: 70px;
  color: #8f90a0;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
}

.header-body {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: auto;
  height: 70px;
}

.header-logo {
  width: 106px;
  height: 40px;
  margin-top: 15px;
  margin-bottom: 15px;
}

.header_nav {
  width: 100%;
}

.nav {
  width: 770px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 3px 0 3px 0;
  font-size: 14px;
}

.nav-item {
  list-style-type: none;
  padding-right: 0px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.nav-item-text {
  padding-right: 0px;
}

.nav-link {
  text-decoration: none;
  color: rgba(31, 32, 65, 0.5);
  font-family: "MontserratRegular";
  font-style: normal;
}

.nav-link:hover {
  color: rgba(31, 32, 65, 0.75);
  font-family: "MontserratRegular";
  font-size: 14px;
  line-height: 17px;
  font-weight: bold;
}

.nav-item-dropdown {
  position: relative;
  display: block;
  padding: 0px;
}

.nav-item-dropdown-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 8px;
}

.nav-arrow {
  font-family: "MaterialIcons";
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 20px;
  margin-left: 0px;
  margin-right: 0px;
  cursor: pointer;
}

.nav-item-dropdown-content {
  position: absolute;
  display: none;
  height: 180px;
  width: 145px;
  background-color: #FFFFFF;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.nav-item-dropdown:hover .nav-item-dropdown-content {
  display: block;
  background-color: #FFFFFF;
}

@media only screen and (min-width: 768px) and (max-width: 1080px) {
  .header {
    height: 100%;
  }

  .header-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
  }
}
@media only screen and (min-width: 320px) and (max-width: 480px) {
  .header {
    height: 100%;
  }

  .header-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
  }

  .nav {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .headerbtn_signin {
    margin-top: 20px;
    margin-bottom: 20px;
  }
}